import React, { useEffect, useState } from "react";
import {ReactComponent as GithubIcon} from '../../assets/icons/github.svg';
import {ReactComponent as GoogleIcon} from '../../assets/icons/google.svg';
import {ReactComponent as LinkedinIcon} from '../../assets/icons/linkedin.svg';
import {ReactComponent as PhoneIcon} from '../../assets/icons/phone.svg';
import Nav from "../../utils/Nav";


function Intro() {
  const [kitSource,setKitSource] = useState(null);

  useEffect(() => {
    const script = document.createElement("script");
    script.src = kitSource;
    script.async = true;
    script.type = "module"
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    }
  })

  const loadKit = (ev,src) => {
    ev.preventDefault();
    window.t = null;
    setKitSource("http://localhost:5500/src/features/StarterKits/"+src);
  }

  let iconColor = 'orange';
  return (
    <div className="bg-gray-900 w-full flex flex-col items-center h-screen bg-me">
      <canvas className="absolute w-full h-full z-0" id="kitCanvas"></canvas>
      <div className="w-full py-10 text-orange-400 z-10 h-full flex items-stretch">
        <div className="flex flex-1 bg-black/40 justify-center">
          <div className="flex flex-wrap w-3/4 gap-10">
            <div className="grow flex-1 w-2/3 flex flex-col justify-center ">
              <h1 className="break-words">Yeabsira <br />Abebe</h1>
              <p className="p-4 text-gray-300">
                Software Engineer
              </p>
              <div className="flex gap-4 p-4">
                <a href='/'><LinkedinIcon fill={iconColor} className="icon" /></a>
                <a href='/'><GithubIcon fill={iconColor} className="icon" /></a>
                <a href='/'><GoogleIcon fill={iconColor} className="icon" /></a>
                <a href='/'><PhoneIcon fill={iconColor} className="icon" /></a>
              </div>
            </div>


            <div className=" flex flex-col text-white justify-between " name='navCont'>
              <div className="flex gap-3 py-5 font-extrabold mb-5">
                <a href="/" onClick={(ev) => Nav(ev,'kit')}>Experiments</a>
                <a href="/" className="text-gray-500" onClick={(ev) => Nav(ev,'about')}>About</a>
                <a href="/" className="text-gray-500" onClick={(ev) => Nav(ev,'blog')}>Blog</a>
              </div>

              <div name='blocks' className="flex flex-1 grow flex-col overflow-hidden">
                
                <div className="flex flex-col flex-1 gap-2 text-orange-300" id='kit'>
                  <a href="/" onClick={(ev) => loadKit(ev,'rain.js')}>Rainbow Rain</a>
                  <a href="/" onClick={(ev) => loadKit(ev,'connection.js')}>Connection</a>
                  <a href="/">RGBall</a>
                </div>
                
                <div id="about" className="hidden max-h-44 overflow-y-auto">
                  I was first introduced to coding when i was grade 8 student when i saw my brothers doing their 11'th grade web design assignment.
                  I started being passionate about computers and how they work ever since. 
                  <p>
                    I then participated in a competition at grade 10 which we lost, but able to
                    gain more interest in the field as i was introduced to php language for the first time by one of our compitetor. Made few bussiness here and there from IT assignments in grade 11 and 12 as we learnt
                    c++ and web development. 
                  </p>
                  <p>
                    In the summer of 2018 while i was waiting to join campus and become a software engineer student, me and my brother decided to start
                    an Ecommerce startup company. We successfully managed to make it up and running as we found more partners and clients. Making profit of it
                    got hard and i decided to focus on my classes as my journey to become a software engineer begins.
                  </p>

                </div>

                <div id="blog" className="hidden">
                  hello
                </div>
              </div>

              <form action="" className="py-5">
                <h4>Got a project? Let's talk.</h4>
                <div className="flex ">
                  <input type="email" placeholder="your email" />
                  <input type='submit' value='request' />
                </div>
              </form>
            </div>

          </div>
        </div>
      </div>
    </div>
  )
}

export default Intro;