import vector from '../utils/vector'
import particle from '../utils/particle'

const Spring = {
	spoint: null,
	part: particle.create((window.innerWidth/2)-200,0,10,Math.PI,.8),
	k: .07,
	l: window.innerHeight/3,
	speed: 0,
	particleImage: null,
	create: function(x,y) {
		const obj = Object.create(this);
		obj.spoint = vector.create(x,y);
		obj.part.friction = .95;
		
		return obj;
	},
	update: function() {
		let sdist = this.spoint.subtract(this.part.position);
		sdist.setLength(sdist.getLength() - this.l);
		let sforce = sdist.multiply(this.k);
	
		// this.part.position.x = Math.sin(this.speed) * 100;
		this.part.velocity.addTo(sforce);
		this.speed += 0.1;

		
		this.part.update();

	},
	render: function(context) {
		context.beginPath();
		context.save();
		context.strokeStyle = context.fillStyle = 'orange';

		context.arc(this.spoint.x,this.spoint.y,2,0,Math.PI*2);
		context.fill();
	
		// context.beginPath()?;
		// context.arc(this.part.position.getX(),this.part.position.getY(),20,0,Math.PI*2);
		// context.fill();
		
		context.beginPath();
		context.moveTo(this.spoint.x,this.spoint.y);
		context.lineTo(this.part.position.getX(),this.part.position.getY());
		context.stroke();
		

	}
}

var springObj = Spring.create(window.innerWidth/2,0);

export {Spring,springObj};