function Nav(ev,id) {
  ev.preventDefault();

  let link = ev.target;
  let parent = link.parentElement;
  for(let link of parent.children)
    link.classList.add('text-gray-500');
  for(let i=0;i<5 && !(parent.getAttribute('name') === 'navCont');i++) {
    // console.log(parent)
    parent = parent.parentElement;
  }


  if(parent.getAttribute('name') === 'navCont') {
    link.classList.remove('text-gray-500')
    // console.log(Array.from(parent.children));
    let blocks = Array.from(parent.children).find((child) => child.getAttribute('name') === 'blocks');
    for(let child of blocks.children) {
      if(child.id === id) {
        child.classList.remove("hidden");
        child.classList.add("inherit");
      }
      else child.classList.add("hidden");
    }
  }
}

export default Nav;